// Related Communities

#related_communities {
  @include u-margin-top(5);
	header{
		@include u-margin(0);
		h3{
			@include u-margin(0);
      @include u-margin-bottom(1);
      @include u-font('sans', 'xs');
      @include u-text('heavy');
		}
	}

  article.promo{
    @include u-margin-bottom(1);
		@include u-bg('gray-1');
    border: 1px #cac9c0 solid;
		h3{
			@include u-font('sans', '2xs');
      @include u-text('gold-30v');
      @include u-line-height('sans', 2);
      z-index: 1;
      a{
        @include u-text('orange-warm-90');
      }
		}
    .members{
      @include u-margin(0);
      @include u-padding-y('05');
      @include u-text('indigo-warm-50v');
      @include u-font('sans', '3xs');
      @include u-text('semibold');
      @include u-text('right');
    }
    &:hover{
      @include u-bg('gray-warm-5');
      h3 a{
        border-bottom: 2px #fc906d solid;
      }
    }
    &:last-child{
      @include u-margin(0);
      @include u-bg('gray-10');
    }
  }
  footer{
    h5{
      @include u-margin(0);
      a{
        @include u-text('normal');
        @include u-text('no-underline');
        @include u-text('indigo-warm-60v');
        &:hover{
          border-bottom: 2px #967efb solid; // $indigo-40
        }
      }
    }
  }
}
